import 'babel-polyfill'
import axios from 'axios'
import qs from 'qs'
import lodash from 'lodash'
import {Message} from 'element-ui'
import Router from '../../config/routerConfig'

const generateApiMap = (map) => {
    let facade = {}
    lodash.forEach(map, function (value, key) {
        facade[key] = toMethod(value)
    })
    return facade
}

const toMethod = (options) => {
    options.method = options.method || 'post'
    return (params = {}, attachedParams, config = {}) => {
        params = lodash.extend(params, attachedParams);
        return sendApiInstance(options.method, options.url, params, config)
    } 
}

const createApiInstance = (config = {}, method, url) => {
    var _config = {
        withCredentials: true, // 跨域
        baseURL: process.env.NODE_ENV == 'development' ? process.env.API_HOST : HOST,
    };
    config = lodash.merge(_config, config);
    return axios.create(config);
}

const errCheck = (code, message, data) => {
    if (code == 200) {
        return true
    }
    return false
}

const sendApiInstance = (method, url, params, config = {}) => {
    if (!url) return; 
    url = url + '?t=' + new Date().getTime();
    console.log(url)
    let instance = createApiInstance(config, method, url);
    instance.interceptors.request.use(
        config => {
            return config;
        },
        error => {
            return Promise.reject(error);
        }
    )

    instance.interceptors.response.use(
        response => {
            let {status, statusText, data} = response;
            if (errCheck(status, statusText, data) && data) {
                return Promise.resolve(data)
            } else {
                return Promise.reject(data).then().catch(res => {
                    var options = {
                        message: res.message,
                        center: true
                    };
                    Message.error(options);
                })
            }
        },
        error => {
            return Promise.reject(error).then().catch(res => {
                if (error.response.status == 401) {
                    /*
                    * 如果登录时间过长 则检测超时状态码401
                    * 清空本地local存储 并且跳转回login页面
                    * */
                    Message.error({
                        message: '登录过期请重新登录',
                        center: true,
                        showClose: true,
                        duration: 2000,
                        onClose: () => {
                            localStorage.clear(); // 清空本地缓存
                        }
                    });
                }
                else {
                    var options = {
                        message: error.response.status + ':' + error.message,
                        center: true
                    };
                    Message.error(options);
                }

            })
        }
    );

    if (method === 'get') {
        params = {
            params: params
        }
    }

    return instance[method](url, params, config)
}

export default {
    generateApiMap
}



